// @flow

import Router from 'next/router';
import {css} from 'styled-components/macro';

const monthNames = [
    'Jan',
    'Feb',
    'March',
    'April',
    'May',
    'June',
    'July',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec'
];

export function upcase(str: Stringable): string {
    return str.toString().toUpperCase();
}

export function downcase(str: Stringable): string {
    return str.toString().toLowerCase();
}

export function capitalize(str: Stringable): string {
    const lowerStr = downcase(str);

    return upcase(lowerStr[0]) + lowerStr.slice(1);
}

export function toPostDate(postDate: Date | string): string {
    if (typeof postDate === 'string') postDate = date(postDate);

    const currentYear = postDate.getFullYear(),
        day = postDate.getDate(),
        month = monthNames[postDate.getMonth()],
        year = currentYear === new Date().getFullYear() ? '' : `, ${currentYear}`;

    return `${month} ${day}${year}`.trim();
}

export function postMeta({date: postDate, path, ...meta}: RawPostMeta): PostMeta {
    return {...meta, href: path.slice(1).replace(/\.\w+$/i, ''), date: date(postDate)};
}

export function date(...args: string[]): Date {
    if (typeof args[0] === 'string') {
        const [dmy, hms] = args[0].split(' ');
        const [dd, mm, yyyy]: number[] = dmy.split('-').map(n => parseInt(n));

        if (hms) {
            const [hh, ii, ss]: number[] = hms.split(':').map(n => parseInt(n));
            return new Date(yyyy, mm - 1, dd, hh, ii, ss);
        }

        return new Date(yyyy, mm - 1, dd);
    }

    return new Date(...args);
}

export function words2minutes(words: number): number {
    const mins = words / 200;

    return mins < 1 ? parseFloat(mins.toFixed(1)) : Math.ceil(mins);
}

export function toggleOnProp(prop: string, a: Stringable, b: Stringable): Stringable {
    return props => (props[prop] ? a : b);
}

export function navigateTo(
    location: string,
    as: string = location,
    opts: Object = {}
): Function {
    return () => Router.push(location, as, opts);
}

export function unitless(str: Stringable): number {
    return parseFloat(str.toString().replace(/[^\d.]+$/, ''));
}

export function fluid(
    prop: string,
    [mins, minw]: string[],
    [maxs, maxw]: string[]
): string {
    const modifier = mins.match(/r?em$/i) || maxs.match(/r?em$/i) ? 16 : 1;

    return css`
        ${prop}: ${mins};

        & {
            @media screen and (min-width: ${minw}) {
                ${prop}: calc(
                    ${mins} + ${unitless(maxs) / modifier - unitless(mins) / modifier} *
                        ((100vw - ${minw}) / (${unitless(maxw)} / ${unitless(minw)}))
                );
            }

            @media screen and (min-width: ${maxw}) {
                ${prop}: ${maxs};
            }
        }
    `;
}

export function rgba(color: string, a: number = 1): string {
    const [R, G, B] = color2rgba(color);
    const A = Math.max(0, Math.min(a, 1));

    return `rgba(${R}, ${G}, ${B}, ${A})`;
}

export function lighten(color: string, percent: number = 0.1): string {
    const [R, G, B, A] = color2rgba(color);
    const X = percent < 0 ? 0 : 255;
    const M = percent * (percent < 0 ? -1 : 1);
    const compile = c => Math.round((X - c) * M) + c;

    return `rgba(${compile(R)}, ${compile(G)}, ${compile(B)}, ${A})`;
}

export function darken(color: string, percent: number = -0.1) {
    return lighten(color, -Math.abs(percent));
}

export function hex2rgba(color: string) {
    const raw = color.replace('#', '');
    const hex =
        raw.length > 3
            ? raw
            : raw
                  .split('')
                  .map(v => `${v}${v}`)
                  .join('');

    const [R, G, B, a] = (hex.match(/[0-9a-f]{2}/gi) || [])
        .slice(0, 4)
        .map(c => parseInt(`0x${c}`));
    const A = (1.0 / 255) * (isNaN(a) ? 255 : a);

    return [R, G, B, A];
}

export function rgb2rgba(color: string) {
    const raw = color.replace(/^rgba?\(|\)$/gi, '');
    const [R, G, B, a] = raw.split(',', 4).map(c => parseInt(c));
    const A = a || 1;

    return [R, G, B, A];
}

export function color2rgba(color: string) {
    return color.startsWith('rgb') ? rgb2rgba(color) : hex2rgba(color);
}
