// @flow

import styled from 'styled-components/macro';
import Post from './layout/post';
import {spacing, colors} from '../config/styles';

type Props = {
    src: string | {src: string},
    wide?: boolean,
    alt?: string,
    width?: Stringable,
    height?: Stringable,
    caption?: boolean,
    captionText?: string
};

export default function Image({
    src,
    width,
    height,
    wide,
    alt,
    caption = true,
    captionText,
    ...rest
}: Props) {
    return (
        <Container {...rest} className={wide ? 'wide' : null}>
            {width && height ? (
                <AspectRatio width={width} height={height}>
                    <img src={src} alt={alt} />
                </AspectRatio>
            ) : (
                <img src={src} alt={alt} />
            )}
            {caption &&
                (captionText ? (
                    <Caption dangerouslySetInnerHTML={{__html: captionText}} />
                ) : alt ? (
                    <Caption>{alt}</Caption>
                ) : null)}
        </Container>
    );
}

export function asImage(data: {src: string}) {
    return function Component(props: {}) {
        return <Image {...data} {...props} />;
    };
}

function AspectRatio({
    children,
    width,
    height
}: {
    children: any,
    width?: Stringable,
    height?: Stringable
}) {
    if (width && height) {
        return (
            <StyledAspectRatio>
                {children}
                <div
                    style={{
                        paddingBottom: `${(
                            (parseFloat(height) / parseFloat(width)) *
                            100
                        ).toFixed(5)}%`
                    }}
                />
            </StyledAspectRatio>
        );
    } else {
        return children;
    }
}

const Caption = styled.figcaption`
    display: block;
    text-align: center;
    margin-top: ${spacing.regular};
    ${colors.withVar('dimgray', 'color')};
    font-size: 0.9rem;
`;

const StyledAspectRatio = styled.div`
    position: relative;
    width: 100%;

    img {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }
}
`;

const Container = styled.figure`
    margin-top: ${spacing.large};
    margin-bottom: ${spacing.large};

    ${Post} &.wide {
        width: 100%;
    }

    img {
        border-radius: 4px;
        display: block;
        width: 100%;
    }
`;
